import React,{useState} from 'react'
import { RegisterContainer,RegisterInner } from '../../style/RegisterCSS'
import firebase from '../../firebase'
import { useNavigate } from 'react-router-dom'
import axios from 'axios'

const Register = () => {
  const navigate = useNavigate()
  const [nameCheck , setNameCheck] = useState(false)
  const [Flag, setFlag] = useState(false);
  const [nameInfo,setNameInfo] = useState('')
  const [userInfo,setUserInfo] = useState({
    name:'',
    email:'',
    pw:'',
    pwCheck:''
  })
  const handleInputValue = (key) => (e) => {
    setUserInfo({...userInfo,[key]: e.target.value})
  }
  const NameCheckFun = (e) =>{
    e.preventDefault()
    if(!userInfo.name){
    return alert(' .')
    }
    let body = {
      displayName : userInfo.name,
    }
    axios.post('/api/user/namecheck',body).then((res)=>{
      if(res.data.success){
        if(res.data.check){
          setNameCheck(true)
          setNameInfo('  .')
        }else{
          setNameInfo('   .')
        }
      }
    })
  } 

  const OnRegister = async (e) =>{
    setFlag(true);
    e.preventDefault()
    const {name,email,pw,pwCheck} = userInfo
    if(!(name && email && pw && pwCheck)){
     return alert('  ')
    }
    if(pw.length < 8){
     return alert(' 8  .')
    }
    if(pw !== pwCheck){
     return alert('    .')
    }
    if(!nameCheck){
      return alert('   !')
    }
    let createdUser = await firebase
    .auth()
    .createUserWithEmailAndPassword(email,pw);
    await createdUser.user.updateProfile({
      displayName: name,
      photoURL: "https://kr.object.ncloudstorage.com/react-community/user/profile.png",
    });
    
    let body = {
      email : createdUser.user.multiFactor.user.email,
      displayName : createdUser.user.multiFactor.user.displayName,
      uid : createdUser.user.multiFactor.user.uid,
      photoURL: "https://kr.object.ncloudstorage.com/react-community/user/profile.png",
    };
    axios.post('/api/user/register',body).then((res)=>{
      setFlag(false);
      if(res.data.success){
        navigate('/login')
      }else{
        alert(' .')
      }
    })
  }

  return (
    <RegisterContainer>
      <RegisterInner>
      <form>
        <label ></label>
        <input placeholder=' !' type='text' onChange={handleInputValue('name')} disabled={nameCheck}></input>
        {nameInfo}
        <button onClick={(e)=>NameCheckFun(e)}>  </button>
        <label ></label>
        <input placeholder=' !' type='text' onChange={handleInputValue('email')}></input>
        <label></label>
        <input minLength={8} placeholder=' !' autoComplete='on' type='password' onChange={handleInputValue('pw')}></input>
        <label> </label>
        <input minLength={8} placeholder=' .' autoComplete='on' type='password' onChange={handleInputValue('pwCheck')}></input>
        <button disabled={Flag} onClick={(e)=>OnRegister(e)}></button>
      </form>
      </RegisterInner>
    </RegisterContainer>
  )
}

export default Register