import React,{useEffect ,useState} from 'react'
import { useParams ,useNavigate } from 'react-router-dom'
import { PostContainer,PostInner } from '../../style/PostCSS'
import Swal from 'sweetalert2'
import axios from 'axios'

const Edit = () => {
  const navigate = useNavigate()
  const params = useParams()
  const [postInfo,setPostInfo] = useState([])
  const [title,setTitle] = useState('')
  const [content,setContent] = useState('')

  useEffect(()=>{
    const body = {
      postNum : params.postNum
    }
    axios.post('/api/post/detail',body).then((res)=>{
      setPostInfo(res.data.post);
    })
  },[])

  useEffect(()=>{
     setTitle(postInfo.title)
     setContent(postInfo.content)
  },[postInfo])



const onSubmit = () => {
  if(title === '' || content === ''){
   return Swal.fire({
      title:'',
      text:'  ',
      icon:'error'
    })
  }
  let body = {
    title: title,
    content: content,
    postNum : params.postNum
  };
  
  axios.post('/api/post/edit',body).then((res)=>{
    if(res.data.success){
      Swal.fire({
        title:'  !',
        icon:"success",
      })
      navigate(`/post/${params.postNum}`)
    }else{
      Swal.fire({
        title:'  !',
        icon:'error'
      })
    }
  })
  .catch((error)=>console.log(error))
}

  return (
    <PostContainer>
      <PostInner>
        <span> </span>
        <input value={title || ''} placeholder='  !' onChange={(e)=>setTitle(e.target.value)}></input>
        <textarea value={content || ''} placeholder='  !' onChange={(e)=>setContent(e.target.value)}></textarea>
        <div>
          <button onClick={onSubmit}></button>
          <button onClick={()=>{navigate(-1)}}></button>
        </div>
      </PostInner>
    </PostContainer>
  )
}

export default Edit