import React,{useState} from 'react'
import {useNavigate} from 'react-router-dom'
import { LoginContainer,LoginInner } from '../../style/LoginCSS'

import firebase from '../../firebase.js'

const Login = () => {
  const navigate = useNavigate()
  const [userInfo,setUserInfo] = useState({
    email:'',
    pw:'',
  })
  const handleInputValue = (key) => (e) => {
    setUserInfo({...userInfo,[key]: e.target.value})
  }
  
  const onLogin = async (e) => {
  const {email,pw} = userInfo
  e.preventDefault()
  if(!(email && pw)){
   return alert(' ')
  }try{
    await firebase.auth().signInWithEmailAndPassword(email,pw)
    navigate('/')
  }catch(error){
   if(error.code === 'auth/user-not-found'){
     alert('   .')
   }else if(error.code === 'auth/wrong-password'){
     alert('  .')
   }else{
     alert(' .')
   }
 }
}
   
  return (
    <LoginContainer>
      <LoginInner>
      <form>
        <label ></label>
        <input placeholder=' !' type='email' onChange={handleInputValue('email')}></input>
        <label></label>
        <input placeholder=' !' autoComplete='on' type='password' onChange={handleInputValue('pw')}></input>
        <button onClick={(e)=>onLogin(e)}></button>
        <button onClick={()=>{navigate('/register')}}></button>
      </form>
      </LoginInner>
    </LoginContainer>
  )
}

export default Login