import React,{useState} from 'react'
import axios from 'axios'
import { useSelector } from 'react-redux'
import { RepleUploadContainer } from '../../style/RepleCSS'

const RepleUpload = (props) => {
  const [reple,setReple] = useState('')
  const user = useSelector((state)=>state.user)

  const SubmitHandler = (e) =>{
   e.preventDefault()
  if(!reple){
    return alert('  ')
  }

  let body = {
    reple : reple,
    uid : user.uid,
    postId:props.postId
  }

  axios.post('/api/reple/submit',body).then((res)=>{
    if(res.data.success){
      alert('  ')
      window.location.reload()
    }else{
      alert('  ')
    }
  })
  }

  return (
    <RepleUploadContainer>
      <form>
        <input placeholder='  ' type='text' value={reple} onChange={(e)=>setReple(e.target.value)}/>
        <button onClick={(e)=>{SubmitHandler(e)}}></button>
      </form>
    </RepleUploadContainer>
  )
}

export default RepleUpload